var clock = new THREE.Clock();

function init(){
    
    const importedModel = new URL('../static/assets/scene.glb', import.meta.url);
    const sizes = {
        width: window.innerWidth,
        height: window.innerHeight
    };
    
    canvas = document.querySelector('.webgl');
    scene = new THREE.Scene();
    renderer = new THREE.WebGLRenderer({
        canvas: canvas,
        alpha: true
    });

    renderer.setSize(sizes.width,sizes.height);

    renderer.setPixelRatio(Math.min(window.devicePixelRatio,2))
    renderer.outputColorSpace = THREE.SRGBColorSpace;
    renderer.toneMapping = THREE.ACESFilmicToneMapping;
    renderer.toneMappingExposure = 0.2;
    renderer.gammaOutput = true;

    const environment = new RoomEnvironment( renderer );
    const pmremGenerator = new THREE.PMREMGenerator( renderer );
    // scene.background = new THREE.Color( 0xbbbbbb );
    scene.environment = pmremGenerator.fromScene( environment ).texture;
    // environment.dispose();

    const grid = new THREE.GridHelper( 500, 10, 0xffffff, 0xffffff );
    grid.material.opacity = 0.5;
    grid.material.depthWrite = false;
    grid.material.transparent = true;
    scene.add( grid );

    
    camera = new THREE.PerspectiveCamera(
        75,
        sizes.width / sizes.height,
        0.1,
        1000
    );

    
    
    camera.position.set(0,3,10);
    // camera.rotation.set(-0.22222,3,Math.PI/2);
    camera.lookAt(new THREE.Vector3(0,3,0));
    scene.add(camera);
    //Adds controls to the canvas
    orbit = new OrbitControls(camera,renderer.domElement);
    orbit.update();

    // orbit.maxAzimuthAngle = [Math.PI, Math.PI];
    // orbit.enablePan = false;
    // orbit.enableDamping = true;
    // orbit.enableRotate = true;
    // orbit.enableZoom = false;

    const color = 0xFFFFFF;

    var light0 = new THREE.AmbientLight(0xffffff,1);
    scene.add(light0);
    
    const assetLoader = new GLTFLoader();
    Promise.all([
        assetLoader.loadAsync(importedModel.href)
    ]).then(models => {
        console.log("Dress Change Model Loaded");
        // get what you need from the models array
        model = models[0].scene;
        console.log(models);
        model.position.set(0,-5,0);
        model.scale.set(0.05,0.05,0.05);
        // mixer = new THREE.AnimationMixer(model);

        // const clips = models[0].animations;

        // clips.forEach(function(clip,i){
        //     action[i] = mixer.clipAction(clip);
        //     action[i].setLoop( THREE.LoopOnce );
        // });
        
        scene.add(models[0].scene);
        renderer.setAnimationLoop(animate);
 
        
    });

}
